<template>
  <nav class="navbar bg-secondary bg-light bg-gradient">
    <router-link to="/" class="nav-link navbar-brand brand"
      >Task Manager</router-link
    >
    <div
      class="col col-custom-logout"
      v-if="['login-cmp', 'sign-up-cmp'].indexOf($route.name) === -1"
    >
      <div class="dropdown">
        <button
          class="btn btn-outline-success dropdown-toggle"
          type="button"
          data-bs-toggle="dropdown"
          aria-expanded="false"
        >
          {{ profileName.value }}
        </button>
        <ul class="dropdown-menu">
          <li>
            <router-link class="dropdown-item" to="/task-manager/profile/editProfile"
              >Edit Profile</router-link
            >
          </li>
        </ul>
      </div>
      <button
        @click.prevent="logout"
        :disabled="loggingout"
        class="btn btn-outline-danger"
      >
        {{ logoutBtnName }}
      </button>
    </div>
  </nav>
</template>
<script>
// import { ref } from "vue";
import { computed } from "vue";
import { useStore } from "vuex";
export default {
  name: "Header_cmp",
  data() {
    // profile_name;
    return {
      loggingout: false,
      logoutBtnName: "log out",
      // profilename: localStorage.getItem("profile-name"),
      // profileName: this.$store.state.profileName,
    };
  },
  computed: {
    profileName() {
      const store = useStore();
      return computed(() => {
        return store.state.profileName;
      });
    },
  },
  watch: {
    profileName(newValue) {
      // This will be called whenever the profileName changes
      console.log("Profile Name changed:", newValue);
    },
  },

  // computed: {
  //   profileName() {
  //     return this.$store.state.profileName;
  //   },
  // },
  // watch:{
  //   profilename:()=>{

  //   }
  // }
  // computed: {
  //   profile_name: () => {
  //     return localStorage.getItem("profile-name");
  //   },
  // },

  methods: {
    async logout() {
      try {
        this.loggingout = true;
        this.logoutBtnName = "loggin out...";
        let config = {
          method: "post",
          maxBodyLength: Infinity,
          url: "https://task-manager-backend-api.onrender.com/users/logout",
          headers: {
            Authorization: `Bearer ${localStorage.getItem("authToken")}`,
          },
        };
        await this.axios.request(config);
        this.loggingout = false;
        this.logoutBtnName = "logout";
        localStorage.clear();
        this.$router.push({ path: "/task-manager/login" });
      } catch (err) {
        this.loggingout = false;
        this.logoutBtnName = "logout";
        this.$router.push({ path: "/task-manager/login" });
      }
    },
  },
};
</script>
<style scoped>
.navbar-custom {
  justify-content: center;
}
.col-custom-brand {
  display: flex;
  justify-content: flex-end;
}
.col-custom-logout {
  display: flex;
  justify-content: flex-end;
  padding-right: 30px;
  gap: 10px;
}
.row {
  width: 100% !important;
}
.brand {
  text-shadow: 2px 2px 4px rgba(40, 143, 95, 0.5);
}
</style>
