<template>
  <div class="card card-custom bg-light mt-3">
    <div class="container mt-2 card-body">
      <Form
        @submit="submitForm"
        :validation-schema="schema"
        v-slot="{ errors }"
      >
        <div class="mb-3">
          <div class="mb-3">
            <label for="name" class="form-label">Furll Name</label>
            <Field
              name="name"
              type="text"
              class="form-control"
              placeholder="Enter your new Name"
              v-model="payload.name"
            />
            <p :style="{ color: 'red', textAlign: 'center' }">
              <span>{{ errors.name }}</span>
            </p>
          </div>
          <div class="mb-3">
            <label for="email" class="form-label">Email Address</label>
            <Field
              name="email"
              type="email"
              class="form-control"
              placeholder="Enter your new Email"
              v-model="payload.email"
            />
            <p :style="{ color: 'red', textAlign: 'center' }">
              <span>{{ errors.email }}</span>
            </p>
          </div>
          <div class="mb-3">
            <label for="password" class="form-label">New Password</label>
            <Field
              name="password"
              type="password"
              class="form-control"
              placeholder="Enter your new Password"
              v-model="payload.password"
            />
            <p :style="{ color: 'red', textAlign: 'center' }">
              <span>{{ errors.password }}</span>
            </p>
          </div>

          <div class="update-btn-custom">
            <button
              :disabled="
                errors.email || errors.password || errors.name || updating
              "
              type="submit"
              class="btn btn-success"
            >
              {{ UpdateBtnName }}
            </button>
            <router-link to="/" class="btn btn-secondary">
              All Tasks
            </router-link>
          </div>
          <br />
          <p v-if="message" :style="{ color: 'green', textAlign: 'center' }">
            <span>{{ message }}</span>
          </p>
          <p v-if="updateError" :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ updateError }}</span>
          </p>
        </div>
      </Form>
    </div>
  </div>
</template>
<script>
import { Form, Field, defineRule } from "vee-validate";
import { required, email } from "@vee-validate/rules";
defineRule("required", required);
defineRule("email", email);
defineRule("pwd_custom", (text) => {
  if (!text || text.length < 6) {
    return "password length should be atleast 6 charecters";
  }
  return true;
});
export default {
  name: "Edit_Profile_cmp",
  components: {
    Form,
    Field,
  },
  data() {
    const schema = {
      email: "",
      name: "",
      password: "",
    };
    return {
      schema,
      UpdateBtnName: "Update Profile",
      updating: false,
      updateError: "",
      payload: {
        name: "",
        email: "",
        password: "",
      },
      message: "",
    };
  },
  mounted() {
    this.payload.email = localStorage.getItem("profile-email") || "";
    this.payload.name = localStorage.getItem("profile-name") || "";
  },
  methods: {
    async submitForm() {
      try {
        this.updating = true;
        this.UpdateBtnName = "Updating...";
        this.message = "";
        this.updateError = "";
        const updatePayload = {};
        for (const [key, value] of Object.entries(this.payload)) {
          if (value) updatePayload[key] = value;
        }
        if (!Object.keys(updatePayload).length) throw new Error();
        let config = {
          method: "patch",
          maxBodyLength: Infinity,
          url: "https://task-manager-backend-api.onrender.com/users/me",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("authToken")}`,
          },
          data: JSON.stringify({ ...updatePayload }),
        };
        await this.axios.request(config);
        if (updatePayload.email) {
          localStorage.setItem("profile-email", updatePayload.email);
        }
        if (updatePayload.name) {
          localStorage.setItem("profile-name", updatePayload.name);
          this.$store.commit("setProfileName", updatePayload.name);
        }
        this.message = "User Details Updated successfully";
        this.updating = false;
        this.UpdateBtnName = "Update Profile";
      } catch (err) {
        console.log(err);
        this.updateError = "Unable to Update at this moment!";
        this.updating = false;
        this.UpdateBtnName = "Update Profile";
      }
    },
  },
};
</script>
<style></style>
