<template>
  <button
    class="btn btn-danger btn-sm"
    @click.prevent="deleteTask"
    :disabled="deletedisabled"
  >
    {{ deleteBtnName }}
  </button>
</template>
<script>
export default {
  name: "Delete_Btn",
  props: {
    taskId: String,
    updateDashBoard: Function,
    // deleteError: String,
  },
  data() {
    return {
      deleteBtnName: "Delete",
      deletedisabled: false,
    };
  },

  methods: {
    async deleteTask() {
      try {
        this.$emit("deleteErr", "");
        this.deleteBtnName = "Deleting...";
        this.deletedisabled = true;
        let config = {
          method: "delete",
          maxBodyLength: Infinity,
          url: `https://task-manager-backend-api.onrender.com/tasks/${this.$props.taskId}`,
          headers: {
            Authorization: `Bearer ${localStorage.getItem("authToken")}`,
          },
        };
        await this.axios.request(config);
        this.deleteBtnName = "Delete";
        this.deletedisabled = false;
        this.$props.updateDashBoard();
      } catch (err) {
        console.log(err);
        this.deleteBtnName = "Delete";
        this.deletedisabled = false;
        // this.$props.deleteError = err.message;
        this.$emit("deleteErr", "Unable to delete the Task!");
      }
    },
  },
};
</script>
<style></style>
