<template>
  <div class="card login-signup-card-custom bg-light mt-3">
    <div class="container mt-2 card-body">
      <Form
        @submit="submitForm"
        :validation-schema="schema"
        v-slot="{ errors }"
      >
        <div class="mb-3">
          <div class="mb-3">
            <label for="name" class="form-label">Furll Name</label>
            <Field
              name="name"
              type="text"
              class="form-control"
              v-model="payload.name"
              placeholder="Enter your Full name"
            />
            <p :style="{ color: 'red', textAlign: 'center' }">
              <span>{{ errors.name }}</span>
            </p>
          </div>
          <div class="mb-3">
            <label for="email" class="form-label">Email Address</label>
            <Field
              name="email"
              type="email"
              class="form-control"
              v-model="payload.email"
              placeholder="Enter your Email"
            />
            <p :style="{ color: 'red', textAlign: 'center' }">
              <span>{{ errors.email }}</span>
            </p>
          </div>
          <div class="mb-3">
            <label for="password" class="form-label">Password</label>
            <Field
              name="password"
              type="password"
              class="form-control"
              v-model="payload.password"
              placeholder="Enter Password (atleast 6 charecters)"
            />
            <p :style="{ color: 'red', textAlign: 'center' }">
              <span>{{ errors.password }}</span>
            </p>
          </div>

          <div class="update-btn-custom">
            <button
              :disabled="
                errors.email || errors.password || errors.name || isLoading
              "
              type="submit"
              class="btn btn-success"
            >
              {{ signupBtnName }}
            </button>
            <router-link to="/task-manager/login" class="btn btn-secondary">
              login
            </router-link>
          </div>
          <br />
          <p v-if="message" :style="{ color: 'green', textAlign: 'center' }">
            <span>{{ message }}</span>
          </p>
          <p v-if="signuperr" :style="{ color: 'green', textAlign: 'center' }">
            <span>{{ signuperr }}</span>
          </p>
        </div>
      </Form>
    </div>
  </div>
</template>
<script>
import { Form, Field, defineRule } from "vee-validate";
import { required, email } from "@vee-validate/rules";
defineRule("required", required);
defineRule("email", email);
defineRule("pwd_custom", (text) => {
  if (!text || text.length < 6) {
    return "password length should be atleast 6 charecters";
  }
  return true;
});
export default {
  name: "Signup_cmp",
  components: {
    Form,
    Field,
  },
  data() {
    const schema = {
      email: "required|email",
      name: "required",
      password: "pwd_custom",
    };
    return {
      signupBtnName: "Sign up",
      payload: {
        name: "",
        email: "",
        password: "",
      },
      isLoading: false,
      signuperr: "",
      schema,
      message: "",
    };
  },
  methods: {
    async submitForm() {
      try {
        console.log("first")
        this.signuperr = "";
        this.signupBtnName = "Sigining up...";
        this.isLoading = true;
        this.message = "";
        let config = {
          method: "post",
          maxBodyLength: Infinity,
          url: "https://task-manager-backend-api.onrender.com/users",
          headers: {
            "Content-Type": "application/json",
          },
          data: JSON.stringify({ ...this.payload }),
        };
        const { data } = await this.axios.request(config);
        localStorage.setItem("profile-name", data.user.name);
        localStorage.setItem("profile-email", data.user.email);
        this.$store.commit("setProfileName", data.user.name);

        // console.log(this.$store.state.profileName);
        localStorage.setItem("authToken", data.token);
        this.isLoading = false;
        this.signupBtnName = "Sign up";
        this.message = "user signup successfully";
        this.$router.push({path:"/"})
      } catch (err) {
        console.log(err)
        this.isLoading = false;
        this.signupBtnName = "Sign up";
        this.signuperr = "Unable to Sign in!";
      }
    },
  },
};
</script>
<style></style>
