<template>
  <div class="card">
    <div class="card-body">
      <div class="container mt-2">
        <div class="row">
          <router-link to="/task-manager/task/create" class="col-2 mx-3 btn btn-warning"
            >Create Task</router-link
          >
          <div class="col custom-search">
            <input
              @keyup="searchBoxHandler($event)"
              type="text"
              placeholder="Search your Task by Name"
              class="form-control custom-input"
            />
            <!-- <button class="btn btn-outline-success">Search</button> -->
          </div>
        </div>
        <div class="row mt-4">
          <div v-if="deleteError.length" class="custom-alert-box">
            <div class="alert alert-danger custom-alert" role="alert">
              {{ deleteError }}
            </div>
          </div>

          <div class="col-lg-12">
            <div v-if="!contentLoaded" class="d-flex justify-content-center">
              <div class="spinner-border text-success" role="status">
                <span class="visually-hidden">Loading...</span>
              </div>
            </div>
            <table
              v-if="contentLoaded"
              class="table table-bordered table-hover text-center"
            >
              <thead class="bg-success bg-gradient">
                <tr>
                  <th>No</th>
                  <th>Title</th>
                  <th>Description</th>
                  <th>Due Date</th>
                  <th>Status</th>
                  <th>Assignee</th>
                  <th>Action</th>
                </tr>
              </thead>

              <tbody class="bg-light bg-gradient">
                <tr v-if="!tasks.length">
                  <td colspan="7">
                    <div class="no-content">
                          <div class="alert alert-warning" role="alert">
                     No Task Created Yet!
                    </div>
                    </div>
                
                  </td>
                </tr>

                <!-- Loop through your tasks and populate the table rows -->
                <tr v-else v-for="(task, index) in tasks" :key="task._id">
                  <td>{{ index + 1 }}</td>
                  <td>{{ task.title }}</td>
                  <td>{{ task.description }}</td>
                  <td>{{ getDate(task.dueDate) }}</td>
                  <td>{{ task.status }}</td>
                  <td>{{ task.assignee }}</td>
                  <td class="gap">
                    <router-link
                      :to="{ name: 'update-task', params: { id: task._id } }"
                      class="btn btn-success btn-sm"
                      >Edit</router-link
                    >
                    <delete-btn
                      :taskId="task._id"
                      :updateDashBoard="updateDashBoard"
                      @deleteErr="deleteError = $event"
                    ></delete-btn>
                  </td>
                </tr>

                <!-- Add more rows as needed -->
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//import getTask from "../handler/GetTask.js";

import moment from "moment";
import Delete_Btn from "../components/DeleteBtn.vue";
export default {
  name: "Dashboard_cmp",
  components: {
    "delete-btn": Delete_Btn,
  },
  data() {
    return {
      deleteError: "",
      contentLoaded: true,
      tasks: [],
    };
  },
  // setup() {
  //   const Filtertasks = ref([]);
  //   watch(sharedState, (val) => {
  //     this.Filtertasks = val.data;
  //   });

  //   return { sharedState };
  // },
  methods: {
    updateDashBoard() {
      this.getTasks();
    },
    getDate(date) {
      return moment(date).format("DD/MM/YYYY");
    },
    deleteTask: async function () {
      // try{
      // }catch(err){
      // }
    },
    async getTasks() {
      try {
        this.contentLoaded = false;
        if (!localStorage.getItem("authToken")) {
          return this.$router.push({ path: "/task-manager/login" });
        }

        const { data } = await this.axios.get(
          "https://task-manager-backend-api.onrender.com/tasks",
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("authToken")}`,
            },
          }
        );
        // console.log(EventBus);

        this.tasks = data;
        this.contentLoaded = true;
      } catch (err) {
        this.tasks = [];
        this.contentLoaded = true;
      }
    },
    searchBoxHandler: async function (event) {
      try {
        if (event.target.value == "") {
          const { data } = await this.axios.get(
            "https://task-manager-backend-api.onrender.com/tasks",
            {
              headers: {
                Authorization: `Bearer ${localStorage.getItem("authToken")}`,
              },
            }
          );
          // console.log(EventBus);

          this.tasks = data;
        } else {
          if (this.tasks.length) {
            return (this.tasks = this.tasks.filter((task) =>
              task.title.toLowerCase().match(event.target.value.toLowerCase())
            ));
          } else return;
        }
      } catch (err) {
        if (err.response.status == 401)
          return this.$router.push({ path: "/task-manager/login" });
      }
    },
  },

  mounted() {
    this.getTasks();
  },
};
</script>
<style>
body {
  background-image: linear-gradient(
    to right,
    rgb(176, 201, 175),
    rgb(225, 217, 195),
    rgb(159, 152, 132)
  );

  margin: 0;
}
.no-content{
  display: flex;
  
  justify-content: center;
}
.card {
  background: none !important;
  border: none !important;
}
.gap {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
}
.custom-search {
  display: flex;
  justify-content: end;
  gap: 5px;
}
input.form-control.custom-input:focus {
  border-color: green;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.25);
}
.bg-success-custom {
  background-color: #51d500 !important;
}
.custom-input {
  width: 300px;
  text-align: center;
  border: 1px solid green;
  font-weight: 400;
}

.custom-alert-box {
  display: flex;
  justify-content: center;
}
.custom-alert {
  height: fit-content;
  padding: 5px;
}
</style>
