import { createRouter, createWebHistory } from 'vue-router/dist/vue-router.esm-bundler'
import Dashboard_cmp from './Screen/Dashboard.vue'
import Create_task_cmp from './Screen/Create.vue'
import Edit_task_cmp from './Screen/Edit.vue'
import Edit_Profile_cmp from './Screen/EditProfile.vue'
import Login_cmp from './Screen/Login.vue'
import Signup_cmp from './Screen/Signup.vue'
export default () => createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: "/",
            redirect: "/task-manager"
        },
        {
            path: "/task-manager",
            name: "Dashboard",
            component: Dashboard_cmp,
        },
        {
            path: "/task-manager/task/create",
            name: "create-task",
            component: Create_task_cmp,
        },
        {
            path: "/task-manager/task/editTask/:id",
            name: "update-task",
            component: Edit_task_cmp
        },
        {
            path: "/task-manager/profile/editProfile",
            name: "edit-profile",
            component: Edit_Profile_cmp
        },
        {
            path: "/task-manager/login",
            name: "login-cmp",
            component: Login_cmp
        },
        {
            path: "/task-manager/signup",
            name: "sign-up-cmp",
            component: Signup_cmp
        },




    ]
})