<template>
  <div class="card card-custom bg-light">
    <div class="container mt-2 card-body">
      <Form @submit="addTask" :validation-schema="schema" v-slot="{ errors }">
        <div class="mb-3">
          <label for="title" class="form-label">Title</label>
          <Field
            v-model="payload.title"
            type="text"
            name="title"
            class="form-control"
            id="title"
            placeholder="Enter task title"
            required
          />
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.title }}</span>
          </p>
        </div>
        <div class="mb-3">
          <label for="description" class="form-label">Description</label>
          <Field
            as="textarea"
            v-model="payload.description"
            class="form-control"
            id="description"
            name="description"
            placeholder="Enter task description"
            rows="3"
          ></Field>
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.description }}</span>
          </p>
        </div>
        <div class="mb-3">
          <label for="dueDate" class="form-label">Due Date</label>
          <Field
            v-model="payload.dueDate"
            type="date"
            class="form-control"
            id="dueDate"
            name="dueDate"
            required
          />
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.dueDate }}</span>
          </p>
        </div>
        <div class="mb-3">
          <label for="status" class="form-label">Status</label>
          <Field
            as="select"
            class="form-select"
            id="status"
            v-model="payload.status"
            name="status"
            required
          >
            <option value="" selected>Select status</option>
            <option value="not_started">Not Started</option>
            <option value="in_progress">In Progress</option>
            <option value="completed">Completed</option>
          </Field>

          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.status }}</span>
          </p>
        </div>
        <div class="mb-3">
          <label for="assignee" class="form-label">Assignee</label>
          <Field
            v-model="payload.assignee"
            type="text"
            class="form-control"
            id="assignee"
            placeholder="Enter assignee's name"
            name="assignee"
            required
          />
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.assignee }}</span>
          </p>
        </div>
        <div class="update-btn-custom">
          <button
            type="submit"
            v-bind:disabled="
              errors.title ||
              errors.description ||
              errors.assignee ||
              errors.dueDate ||
              errors.status ||
              loader
            "
            class="btn btn-success"
          >
            {{ buttonName }}
          </button>
          <router-link to="/" class="btn btn-secondary">
            All Tasks
          </router-link>
        </div>
        <p
          v-if="successMessage.length"
          :style="{ color: 'green', textAlign: 'center' }"
        >
          <span>
            {{ successMessage }}
          </span>
        </p>
        <p v-if="errStr.length" :style="{ color: 'red', textAlign: 'center' }">
          <span>
            {{ errStr }}
          </span>
        </p>
      </Form>
    </div>
  </div>
</template>

<script>
import { Form, Field, defineRule } from "vee-validate";
import { required } from "@vee-validate/rules";
defineRule("required", required);
export default {
  name: "Create_task_cmp",
  components: {
    Form,
    Field,
  },
  data() {
    const schema = {
      title: "required",
      description: "required",
      dueDate: "required",
      status: "required",
      assignee: "required",
    };
    return {
      schema,
      loader: false,
      err: false,
      successMessage: "",
      buttonName: "Add Task",
      errStr: "",
      payload: {
        title: "",
        description: "",
        dueDate: "",
        status: "",
        assignee: "",
      },
    };
  },
  mounted: () => {
    if (!localStorage.getItem("authToken")) {
      return this.$router.push({ path: "/task-manager/login" });
    }
  },
  methods: {
    // generateDueDate(){

    // },
    async addTask() {
      try {
        this.err = false;
        this.successMessage = "";
        this.errStr = "";
        this.buttonName = "Adding...";
        this.loader = true;
        const addPayload = { ...this.payload };
        for (let key in addPayload) {
          if (addPayload[key] == "") {
            throw new Error("Fields can't be empty");
          } else if (key == "dueDate") {
            addPayload[key] = new Date(addPayload[key]).toISOString();
          }
        }

        await this.axios.post(
          "https://task-manager-backend-api.onrender.com/tasks",
          addPayload,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("authToken")}`,
            },
          }
        );

        this.loader = false;
        this.buttonName = "Add Task";
        this.successMessage = "Task Added Successfully!";
      } catch (err) {
        console.log(err);
        this.buttonName = "Add Task";
        this.errStr = err.message;
        this.loader = false;
        this.err = true;
      }
    },
  },
};
</script>
<style>
.error-class {
  color: red;
  width: 100%;
  text-align: center;
  font-weight: bolder;
}
</style>
