<template>
  <div class="card login-signup-card-custom bg-light mt-3">
    <div class="container mt-2 card-body">
      <Form @submit="login" :validation-schema="schema" v-slot="{ errors }">
        <div class="mb-3">
          <div class="mb-3">
            <label for="email" class="form-label">Email Address</label>
            <Field
              name="email"
              type="email"
              class="form-control"
              v-model="email"
            />
            <p :style="{ color: 'red', textAlign: 'center' }">
              <span>{{ errors.email }}</span>
            </p>
          </div>
          <div class="mb-3">
            <label for="password" class="form-label">Password</label>
            <Field
              name="password"
              type="password"
              class="form-control"
              v-model="password"
            />
            <p :style="{ color: 'red', textAlign: 'center' }">
              <span>{{ errors.password }}</span>
            </p>
          </div>

          <div class="update-btn-custom">
            <button
              :disabled="errors.email || errors.password || disabledBtn"
              class="btn btn-success"
            >
              {{ loginBtnName }}
            </button>
            <router-link to="/task-manager/signup" class="btn btn-secondary">
              sign up
            </router-link>
          </div>
          <p class="mt-2" :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ loginFailed }}</span>
          </p>
        </div>
      </Form>
    </div>
  </div>
</template>

<script>
import { Form, Field, defineRule } from "vee-validate";
import { required, email } from "@vee-validate/rules";
//console.log(this.$store);
defineRule("required", required);
defineRule("email", email);
export default {
  name: "Login_cmp",
  components: {
    Form,
    Field,
  },
  data() {
    const schema = {
      email: "required|email",
      password: "required",
    };
    return {
      email: "",
      password: "",
      loginBtnName: "login",
      schema,
      message: "",
      loginFailed: "",
      disabledBtn: false,
    };
  },

  mounted() {
    if (localStorage.getItem("authToken"))
      return this.$router.push({ path: "/" });
  },
  methods: {
    async login() {
      try {
        this.disabledBtn = true;
        this.loginFailed = "";
        this.loginBtnName = "loggin...";
        const { data } = await this.axios.post(
          "https://task-manager-backend-api.onrender.com/users/login",
          {
            email: this.email,
            password: this.password,
          }
        );
        localStorage.setItem("profile-name", data.user.name);
        localStorage.setItem("profile-email", data.user.email);
        this.$store.commit("setProfileName", data.user.name);
    
        // console.log(this.$store.state.profileName);
        localStorage.setItem("authToken", data.token);
        this.disabledBtn = false;
        this.loginBtnName = "login";
        this.$router.push({ path: "/" });
      } catch (err) {
        this.loginFailed = err.response.data.error.message;
        this.disabledBtn = false;
        this.loginBtnName = "login";
      }
    },
  },
};
</script>

<style>
.login-signup-card-custom {
  height: fit-content;
  display: flex;
  width: 450px;
  margin: auto;
}
</style>
