<template>
  <div class="card card-custom bg-light mt-2">
    <div class="container mt-2 card-body">
      <div v-if="isLoading" class="d-flex justify-content-center">
        <div class="spinner-border text-success" role="status">
          <span class="visually-hidden">Loading...</span>
        </div>
      </div>
      <Form
        @submit="updateTask"
        :validation-schema="schema"
        v-slot="{ errors }"
        v-else
      >
        <div class="mb-3">
          <label for="title" class="form-label">Title</label>
          <Field
            type="text"
            class="form-control"
            name="title"
            id="title"
            v-model="payload.title"
          />
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.title }}</span>
          </p>
        </div>
        <div class="mb-3">
          <label for="description" class="form-label">Description</label>
          <Field
            as="textarea"
            class="form-control"
            name="description"
            id="description"
            v-model="payload.description"
            rows="3"
          ></Field>
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.description }}</span>
          </p>
        </div>
        <div class="mb-3">
          <label for="dueDate" class="form-label">Due Date</label>
          <Field
            type="date"
            class="form-control"
            id="dueDate"
            name="dueDate"
            v-model="payload.dueDate"
          />
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.dueDate }}</span>
          </p>
        </div>
        <div class="mb-3">
          <label for="status" class="form-label">Status</label>
          <Field
            as="select"
            class="form-select"
            id="status"
            name="status"
            v-model="payload.status"
          >
            <option value="not_started">Not Started</option>
            <option value="in_progress">In Progress</option>
            <option value="completed">Completed</option>
          </Field>
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.status }}</span>
          </p>
        </div>
        <div class="mb-3">
          <label for="assignee" class="form-label">Assignee</label>
          <Field
            type="text"
            class="form-control"
            id="assignee"
            name="assignee"
            v-model="payload.assignee"
            required
          />
          <p :style="{ color: 'red', textAlign: 'center' }">
            <span>{{ errors.assignee }}</span>
          </p>
        </div>
        <div class="update-btn-custom">
          <button
            type="submit"
            :disabled="
              errors.title ||
              errors.description ||
              errors.assignee ||
              errors.dueDate ||
              errors.status ||
              updating
            "
            class="btn btn-success"
          >
            {{ buttonName }}
          </button>
          <router-link to="/" class="btn btn-secondary">
            All Tasks
          </router-link>
        </div>
        <p
          v-if="errorStr.length"
          :style="{ color: 'red', textAlign: 'center' }"
        >
          <span>{{ errorStr }}</span>
        </p>
        <p
          v-if="successMessage.length"
          :style="{ color: 'green', textAlign: 'center' }"
        >
          <span>{{ successMessage }}</span>
        </p>
      </Form>
    </div>
  </div>
</template>
<script>
import moment from "moment";
import { Form, Field, defineRule } from "vee-validate";
import { required } from "@vee-validate/rules";
defineRule("required", required);
// import {ref} from 'vue'
// const disabledfield=ref(false)
export default {
  name: "Edit_task_cmp",
  components: {
    Form,
    Field,
  },
  data() {
    const schema = {
      title: "required",
      description: "required",
      dueDate: "required",
      status: "required",
      assignee: "required",
    };
    return {
      errorBool: false,
      errorStr: "",
      isLoading: false,
      successMessage: "",
      updating: false,
      buttonName: "Update Task",
      schema,
      payload: {
        title: "",
        description: "",
        dueDate: "",
        status: "",
        assignee: "",
      },
    };
  },

  methods: {
    async updateTask() {
      try {
        this.buttonName = "Updating...";
        this.errorBool = false;
        this.updating = true;
        this.successMessage = "";
        this.errorStr = "";
        const updatePayload = { ...this.payload };
        for (let key in updatePayload) {
          if (updatePayload[key] == "") {
            throw new Error("Fields can't be empty");
          } else if (key == "dueDate") {
            updatePayload[key] = new Date(updatePayload[key]).toISOString();
          }
        }
        await this.axios.patch(
          `https://task-manager-backend-api.onrender.com/tasks/${this.$route.params.id}`,
          updatePayload,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("authToken")}`,
            },
          }
        );
        this.successMessage = "Task Updated Scussessfully";
        this.buttonName = "Update Task";
        this.updating = false;
      } catch (err) {
        console.log(err);
        this.errorBool = true;
        //this.successMessage = "";
        this.buttonName = "Update Task";
        this.updating = false;
        this.errorStr = err.message;
      }
    },
  },
  async mounted() {
    try {
      if (!localStorage.getItem("authToken")) {
        return this.$router.push({ path: "/task-manager/login" });
      }
      //console.log(this.$route.params);
      this.isLoading = true;
      const { data: foundDetails } = await this.axios.get(
        `https://task-manager-backend-api.onrender.com/tasks/${this.$route.params.id}`,
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("authToken")}`,
          },
        }
      );
      //let foundDetails = data.find((task) => task.id == this.$route.params.id);
      if (!foundDetails) throw new Error("Unable to find the details");
      for (let key in this.payload) {
        if (key == "dueDate") {
          foundDetails[key] = moment(foundDetails[key]).format("YYYY-MM-DD");
        }
        this.payload[key] = foundDetails[key];
      }
      this.isLoading = false;
    } catch (err) {
      if (err.response.status == 401) {
        this.$router.push("/task-manager/login");
      }
    }
  },
};
</script>
<style>
.card-custom {
  height: fit-content;
  display: flex;
  width: 550px;
  margin: auto;
}
.update-btn-custom {
  display: flex;
  justify-content: center;
  gap: 5px;
}
label {
  font-weight: bold;
}
.error-class {
  color: red;
  width: 100%;
  text-align: center;
  font-weight: bolder;
}
</style>
